import api from '@/common/index.js';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import '@/common/uni.css';
var mvc = {
	saleList: [],
	loadingType: 'more', //加载更多状态
	cartoon: false,
	page: 1, //当前页
	type: 'add'
}
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			mvc: {
				saleList: [],
				loadingType: 'more', //加载更多状态
				cartoon: false,
				page: 1, //当前页
				type: 'add'
			}
		}
	},
	onLoad() {

	},
	computed: {
		i18n() {
			return this.$t('send');
		}
	},
	onShow(e) {
		let that = this
		setTimeout(function() {
			api.pageTitle(that.$t('send').title)
			api.load(that.$t('index').loadmsg)
			that.loadData();
		}, 300)

	},
	methods: {
		loadData() {
			let that = this;
			let params = {
				p: 1
			}
			that.$ajax.logisList(params).then(res => {
				let list = res.list;
				if (list == null || res.other.totalPage == 0) {
					uni.hideLoading();
					mvc.cartoon = true;
					mvc.loadingType = "nomore";
					return false;
				} else {
					list.forEach(function(item, index) {
						if (item.status == 1) {
							item.status = "已清点"
						};
						if (item.status == 2) {
							item.status = "完成"
						};
						if (item.status == 0) {
							item.status = "待审核"
						};
					})
					mvc.saleList = list;
					// that.mvc = mvc;
					that.pagenum(res);
				}
			})
		},
		pagenum(res) {
			let that = this;
			mvc.loadingType = res.other.thispage > res.other.totalPage ? 'nomore' : 'more';
			//请求分页
			if (mvc.type == 'add') {
				mvc.page = mvc.page + 1;

				if (mvc.loadingType === 'nomore') {
					that.mvc = mvc;
					return;
				}
				mvc.loadingType = 'loading';
			} else {
				mvc.loadingType = 'more'
			}

			// 刷新页面
			if (mvc.type == 'refresh') {
				mvc.saleList = [];
				uni.stopPullDownRefresh();
				mvc.page = mvc.page + 1;

				if (mvc.loadingType === 'nomore') {
					that.mvc = mvc;
					return;
				}
				mvc.loadingType = 'loading';
			}
			// 当前页面小于20条
			if (mvc.saleList.length < 20) {
				mvc.loadingType = 'nomore';
				uni.hideLoading();
				mvc.saleList = mvc.saleList.concat(mvc.saleList);
				that.mvc = mvc;
				return false;
			}
			uni.hideLoading();
			
			mvc.saleList = mvc.saleList.concat(mvc.saleList);
			that.mvc = mvc;
		},
		//下拉刷新
		onPullDownRefresh() {
			mvc.page = 1;
			mvc.type = 'refresh';
			this.loadData();
		},
		//加载更多
		onReachBottom() {
			mvc.type = 'add'
			this.loadData();
		
		},
		details(e) {
			uni.navigateTo({
				url: '/pages/logistics-detail/logistics-detail?id=' + e
			})

		},

		navlogistics(e) {
			uni.navigateTo({
				url: '/pages/logistics-add/logistics-add'
			})
		}
	}
}
